.app {
    background-color: var(--primary-color);
    font-family: var(--font-base);
}

.app__whitebg {
    background-color: var(--white-color);
}

.app__primarybg {
    background-color: var(--primary-color);
}

.app__container {
    width: 100%;
    min-height: 100vh;
    display: flex;
    flex-direction: row;
}

.app__flex {
    display: flex;
    justify-content: center;
    align-items: center;
}

.app__wrapper {
    flex: 1;
    width: 100%;
    flex-direction: column;
    padding: 4rem 2rem;
    @media screen and (max-width: 450px) {
        padding: 4rem 1rem 2rem;
    }
}

.copyright {
    width: 100%;
    padding: 2rem 0 0;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-end;
    p {
        text-transform: uppercase;
        color: var(--black-color);
    }
}

.head-text {
    font-size: 1.5rem;
    font-weight: 200;
    text-align: center;
    color: var(--black-color);
    text-transform: capitalize;
    span {
        color: var(--secondary-color);
    }
    @media screen and (min-width: 2000px) {
        font-size: 3rem;
    }
    @media screen and (max-width: 450px) {
        font-size: 0.75rem;
    }
}

.head-text2 {
    font-size: 2.5rem;
    font-weight: 300;
    text-align: center;
    color: var(--secondary-color);
    text-transform: capitalize;
    margin: 0.4rem 0 0.4rem 0;
    @media screen and (min-width: 2000px) {
        font-size: 4rem;
    }
    @media screen and (max-width: 450px) {
        font-size: 1rem;
        margin: 0.2rem 0 0.2rem 0;
    }
}

.head-text-small {
    font-size: 1rem;
    font-weight: 200;
    text-align: center;
    color: var(--gray-color);
    text-transform: capitalize;
    @media screen and (min-width: 2000px) {
        font-size: 3rem;
    }
    @media screen and (max-width: 450px) {
        font-size: 0.7rem;
    }
}

.p-text {
    font-size: 0.8rem;
    text-align: left;
    color: var(--gray-color);
    line-height: 1.5;
    @media screen and (min-width: 2000px) {
        font-size: 1.75rem;
    }
}

.bold-text {
    font-size: 1rem;
    font-weight: 800;
    color: var(--black-color);
    text-align: left;
    @media screen and (min-width: 2000px) {
        font-size: 2rem;
    }
    @media screen and (max-width: 450px) {
        font-size: 0.9rem;
    }
}

.app__social {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    flex-direction: column;
    padding: 1rem;
    div {
        width: 40px;
        height: 40px;
        border-radius: 50%;
        background-color: transparent;
        margin: 0.25rem 0;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: all 0.3s ease-in-out;
        svg {
            width: 20px;
            height: 20px;
            color: var(--gray-color);
        }
        &:hover {
            // background-color: var(--secondary-color);
            // border-color: var(--secondary-color);
            svg {
                // color: var(--white-color);
                color: var(--secondary-color);
            }
        }
        @media screen and (min-width: 2000px) {
            width: 70px;
            height: 70px;
            margin: 0.5rem 0;
            svg {
                width: 30px;
                height: 30px;
            }
        }
    }
}

.app__navigation {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 1rem;
    .app__navigation-dot {
        width: 10px;
        height: 10px;
        border-radius: 50%;
        background-color: #cbcbcb;
        margin: 0.5rem;
        transition: background-color 0.2s ease-in-out;
        &:hover {
            background-color: var(--secondary-color);
        }
        @media screen and (min-width: 2000px) {
            width: 20px;
            height: 20px;
        }
    }
}

@media screen and (max-width: 500px) {
    .app__navigation,
    .app__social {
        display: none;
    }
    .copyright {
        padding: 2rem;
    }
}